
import { computed, defineComponent, inject } from '@vue/composition-api';
import { getCurrentCompany } from '@/loaders/company';
import { MaterialGoods } from '@/models/MaterialGood';
import open from '@/utils/open-window';
import OmittedDisplayer from '@/components/OmittedDisplayer.vue';

export default defineComponent({
  props: {
    materialInfo: {
      type: Object as () => MaterialGoods,
      required: true,
    },
    displayMode: {
      type: String as () => 'drape' | 'flat',
      default: 'drape',
    },
    // urlPrefix: {
    //   type: String,
    //   required: true,
    // },
    operations: {
      type: Array as () => number[],
      default: () => [0, 1, 2],
    },
  },
  render() {
    const generateOperations = (ops: number[]) => {
      return [
        <li class='flex-vertical-center li-option' onclick={this.goToCompany}>
          <i class='i-shangjia'></i>
          <div>{this.$t('material-mall.supplier-page')}</div>
        </li>,
        <li class='flex-vertical-center li-option' onclick={this.goToColorBoard}>
          <i class='i-seka'></i>
          <div>{this.$t('material-mall.material_swatch')}</div>
        </li>,
        <li class='flex-vertical-center li-option' onclick={this.goToSearchSimilar}>
          <i class='i-yitusoutu'></i>
          <div>{this.$t('material-mall.similar_search')}</div>
        </li>,
      ].filter((item, idx) => ops.includes(idx));
    };
    return <figure class={['container', 'flex-column', { flat: this.displayMode === 'flat' }]} draggable={false} onclick={this.handleClickFigure}>
      <div class='magnify' onclick={(e: Event) => this.$act(() => {
        this.$emit('preview');
        e.stopPropagation();
      })}>
        {
          // <i class='tri'></i>
        }

        <i class='i-fangdajing'></i>
      </div>
      {
        this.operations.length
          ? <el-popover trigger="hover" visible-arrow={false} popper-class='commodity-options'>
            <ul class='flex-column'>
              {generateOperations(this.operations)}
            </ul>
            <i class='more-options i-gengduo1' slot='reference' onclick={(e: Event) => e.stopPropagation()}></i>
          </el-popover> : null
      }
      <base-image src={this.imgSrc} class={{ 'material-picture': true }}
        fit={this.displayMode === 'flat' ? 'cover' : 'scale-down'} thumb={{ width: 204, height: 204 }}></base-image>
      {
        this.materialInfo.isCloudRenderComplete !== 1
          ? <span class='material-render-flag'>{this.$t('material-mall.preview_picture')}</span>
          : null
      }
      <figcaption class='flex-center material-name'>
        <OmittedDisplayer width={224} outsideContent={this.materialInfo.materialName}>
          {this.materialInfo.materialName}
        </OmittedDisplayer>
      </figcaption>
    </figure>;
  },
  setup(props, { emit, root }) {
    const isQtInjection = inject('isQt') as () => boolean;
    const urlPrefix = isQtInjection() ? '/qt' : '';
    const imgSrc = computed(() => {
      if (props.displayMode === 'flat') {
        return props.materialInfo.imgJson.texture ?? props.materialInfo.imgJson.flat;
      }
      return props.materialInfo.imgJson.thumbnail;
    });
    const company = computed(() => getCurrentCompany());
    function handleClickFigure() {
      if (company.value!.type === 3) {
        root.$router.push({
          path: `${urlPrefix}/supplier-backend/preview/goods-detail`,
          query: {
            ...root.$route.query,
            id: props.materialInfo.id,
          },
        });
      } else if (urlPrefix) {
        root.$router.push({
          path: '/qt/material-mall/goods-detail',
          query: {
            id: props.materialInfo.id,
          },
        });
      } else {
        open({
          path: '/material-mall/goods-detail',
          query: {
            id: props.materialInfo.id,
          },
        });
      }
    }

    function goToCompany(e: Event) {
      e.stopPropagation();
      root.$router.push({
        path: `${urlPrefix}/supplier-page/preview/homepage`,
        query: {
          companyId: props.materialInfo.companyId,
        },
      });
    }

    function goToColorBoard(e: Event) {
      e.stopPropagation();
      root.$router.push({
        path: `${urlPrefix}/material-mall/colorboard`,
        query: {
          supplierArticleName: props.materialInfo.supplierArticleName,
        },
      });
    }
    function goToSearchSimilar(e: Event) {
      e.stopPropagation();
      const searchSimilarPath = `${urlPrefix}/material-mall/searchsimilar`;
      const query = {
        colorFamily: props.materialInfo.colorFamily,
        type: props.materialInfo.materialType,
        id: props.materialInfo.id,
      };
      if (root.$route.path === searchSimilarPath) {
        root.$router.replace({
          path: searchSimilarPath,
          // @ts-ignore
          query,
        });
      } else {
        root.$router.push({
          path: searchSimilarPath,
          // @ts-ignore
          query,
        });
      }
    }
    return {
      imgSrc,
      company,
      handleClickFigure,

      goToCompany,
      goToColorBoard,
      goToSearchSimilar,
    };
  },
});
