<template>
  <el-tooltip :disabled="!isTextEllipsis" :content="outsideContent || textContent" :tabindex="null">
    <span :style="`max-width: ${_width}`" class="omitted-displayer" ref="nameWrapper">
      <slot></slot>
    </span>
  </el-tooltip>
</template>

<script>
export default {
  props: {
    width: {
      type: [Number, String],
      default: 200,
    },
    outsideContent: {
      default: '',
      type: String,
    },
  },
  computed: {
    _width() {
      return typeof this.width === 'number'
        ? `${this.width}px`
        : this.width;
    },
  },
  data() {
    return {
      isTextEllipsis: null,
      textContent: null,
    };
  },
  mounted() {
    /** 用于递归向上判断元素是否可见，由于部分vue实例处于未匹配路由或未激活的状态，使用display：none
     * 来实现的，所以通过这个来判断组件是否处于显示状态
     */
    function judgeIsVisible(node) {
      if (node === document.documentElement) return { visible: true };
      if (node.style.display !== 'none') return judgeIsVisible(node.parentNode);
      return { visible: false, pendingDom: node };
    }
    /** 用于判断可见性，若可见，则获取必要的数据（长度是否超过限度，完整内容是什么）
     * 分为初始化时调用、MutationObserver回调两种情况 */
    const judgeVisibilityThenInit = (_, observer) => {
      if (observer) observer.disconnect();
      const res = judgeIsVisible(this.$el);
      if (!res.visible) {
        const dom = res.pendingDom;
        const obs = new MutationObserver(judgeVisibilityThenInit);
        obs.observe(dom, { attributes: true, attributeFilter: ['style'] });
        return;
      }
      this.isTextEllipsis = this.$refs.nameWrapper?.offsetWidth
        < this.$refs.nameWrapper?.scrollWidth;
      this.textContent = this.$el.innerHTML;
    };
    judgeVisibilityThenInit();
  },
};
</script>

<style scoped>
.omitted-displayer {
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>
