
import { defineComponent, watch, ref } from '@vue/composition-api';
import i18n from 'i18next';
import ButtonHome from '@/components/button/ButtonHome.vue';
import { getContextUser } from '@/loaders/context';

const header = defineComponent({

  render() {
    const self = this as InstanceType<typeof header>;
    const { isTopPage, currentPage } = self;
    const companyId = getContextUser()?.companyId;
    const isQtPath = self.$route.path.startsWith('/qt/') ? '/qt' : '';
    // const backToPath = { path: 'homepage', query: self.$route.query };
    return (
      <nav class={{ 'profile-nav': true, 'qt-page': isQtPath === '/qt' }}>
        {isQtPath !== '/qt' ? <ButtonHome class='button-home-position' /> : ''}

        {!isTopPage ? <header class='header-back flex-vertical-center'>
          <span style='cursor:pointer;' onclick={() => self.$router.back()}>
            <i class='el-icon-arrow-left'></i>
            {i18n.t('material-mall.previous')}
          </span>
          <span>&nbsp;|&nbsp;{currentPage}</span>
        </header> : ''}

        <ul class="profile-nav-list">
          <router-link tag='li' class={{
            'profile-nav-item': true,
            // vuerouter的自动active不知为何不生效，强行加个判断
            'router-link-active': self.$route.path.endsWith('/goods-manager/goods-edit'),
          }}
            to={{
              path: `${isQtPath}/supplier-backend/goods-manager`,
              query: {
                companyId,
              },
            }}>{i18n.t('supplier-backend.products')}</router-link>
          <router-link tag='li' class="profile-nav-item"
            to={{
              path: `${isQtPath}/supplier-backend/preview`,
              query: {
                companyId,
              },
            }}>{i18n.t('supplier-backend.supplier_material')}</router-link>
          <router-link tag='li' class="profile-nav-item"
            to={{
              path: `${isQtPath}/supplier-backend/statistics`,
              query: {
                companyId,
              },
            }}>{i18n.t('supplier-backend.count_statistics')}</router-link>
          <router-link tag='li' class="profile-nav-item"
            to={{
              path: `${isQtPath}/supplier-backend/company-info`,
              query: {
                companyId,
                editable: 'true',
              },
            }}>{i18n.t('supplier-backend.supplier_brief')}</router-link>
          <router-link tag='li' class="profile-nav-item"
            to={{
              path: `${isQtPath}/supplier-backend/mall-template`,
              query: {
                companyId,
              },
            }}>{i18n.t('supplier-backend.mall_template')}</router-link>
        </ul>
      </nav>
    );
  },

  setup(props, { root }) {
    const isTopPage = ref(true);
    const currentPage = ref(i18n.t('supplier-backend.supplier_info') as String);
    watch(() => root!.$route, (newRoute, oldRoute) => {
      // 排除一级页面（主tab）不显示返回键
      isTopPage.value = [
        '/supplier-backend/goods-manager/home',
        '/supplier-backend/preview/homepage',
        '/supplier-backend/company-info',
        '/supplier-backend/statistics',
        '/supplier-backend/mall-template',
      ].some(endPath => newRoute.path.endsWith(endPath));
      if (newRoute.path.endsWith('/preview/materials')) {
        if (newRoute.query.supplierArticleName) {
          currentPage.value = i18n.t('supplier-backend.preview.material_swatch');
        } else {
          currentPage.value = typeof newRoute.query.pageName === 'string' ? newRoute.query.pageName : i18n.t('supplier-backend.supplier_material');
        }
      } else if (newRoute.path.endsWith('/goods-detail')) {
        currentPage.value = i18n.t('supplier-backend.goods-manager.view_product');
      } else if (newRoute.path.endsWith('/goods-manager/goods-edit')) {
        currentPage.value = i18n.t('supplier-backend.goods-manager.edit_product');
      }
    }, {
      immediate: true,
    });

    return {
      isTopPage,
      currentPage,
    };
  },
});
export default header;
